import React, {Component} from "react";
import "../css/agr.css";
export default class Agreement extends Component {
    render() {
        return (
            <section className="agreement">
                <div className="header">
                    <div className="status">
                        <span></span>
                        <span className="img" />
                    </div>
                    <span className="protolNum">XXXXXXXXX</span>
                </div>
                <div className="content">
                    <h2 className="title"></h2>
                    <p className="detail">
                        <span className="highlight"></span><span className="space" />{" "}
                        <span className="highlight"></span><span className="highlight"></span><span className="highlight">
                            
                        </span><span className="highlight"></span><span className="highlight">
                            
                        </span><span className="highlight"></span><span className="highlight">
                            
                        </span>
                        <span className="highlight"></span>
                    </p>
                    <p className="detail">
                        <span className="highlight"></span>
                    </p>
                    <p className="detail">
                        <span></span>
                        <span className="img" />
                    </p>
                </div>
                <div className="tip">
                    <p className="detail">
                        <br />1 ... <br />2
                    </p>
                </div>
            </section>
        );
    }
}



