import React, {Component} from "react";
import {Link} from "react-router-dom";
import {List, InputItem, Picker, Checkbox, WhiteSpace, WingBlank, Button} from "antd-mobile";
import "../css/receipt.css";

import {RATE, PURPOST} from "../config/rate.config";
import {regObj} from "../config/reg.config";
import {dateFormat, isCanvasBlank} from "../lib/tools";
import Signature from "../lib/Signature";
import "whatwg-fetch";

const AgreeItem = Checkbox.AgreeItem;

const nowTimeStamp = Date.now();

function calcInterest(rate, count, cycle) {
    return parseFloat(rate, 10) * count * cycle / 365;
}

let signInstance;

export default class Write extends Component {
    constructor(props) {
        super(props);
        this.state = {
            borrowerName: "",
            borrowerNameErr: false,
            borrowerId: "",
            borrowerIdErr: false,
            borrowerSignature: "",
            lenders: "",
            lendersErr: false,
            borrowerAmount: 0,
            borrowerAmountErr: false,
            borrowerCycle: 0,
            borrowerCycleErr: false,
            borrowDate: nowTimeStamp,
            repaymentDate: nowTimeStamp,
            rateValue: RATE[0][0].value,
            interest: 0.0,
            purpostValue: PURPOST[0][0].value,
            isBtnDisable: true,
            AgreeItemChecked: true,
            layerShow: false,
            signatureStr: ""
        };
    }

    componentDidMount() {
        // this.addSinatureErrTip(true);
    }

    postData() {
        console.log();
    }

    onBorrowerAmount(borrowerAmount) {
        let {rateValue, borrowerCycle} = this.state;
        this.calcInterest(rateValue, borrowerAmount, borrowerCycle);
    }

    onBorrowerCycle(borrowerCycle) {
        let {rateValue, borrowerAmount} = this.state;
        this.calcInterest(rateValue, borrowerAmount, borrowerCycle);
    }

    onRateChange(rateValue) {
        let {borrowerAmount, borrowerCycle} = this.state;
        this.setState({
            rateValue
        });
        this.calcInterest(rateValue, borrowerAmount, borrowerCycle);
    }

    calcInterest(rateValue, borrowerAmount, borrowerCycle) {
        let interest = calcInterest(rateValue, borrowerAmount, borrowerCycle).toFixed(2);
        this.setState({
            interest
        });
    }

    checkInput(target, value, type) {
        let newState = {
            [target]: value,
            [target + "Err"]: !regObj[type].test(value)
        };
        this.setState(newState);
    }

    checkAllStatu() {
        setTimeout(() => {
            let {
                borrowerName,
                borrowerId,
                lenders,
                borrowerAmount,
                borrowerCycle,
                AgreeItemChecked,
                signatureStr
            } = this.state;

            let isBtnDisable = true;

            if (
                !!borrowerName &&
                !!borrowerId &&
                !!lenders &&
                !!borrowerAmount &&
                !!borrowerCycle &&
                AgreeItemChecked &&
                signatureStr !== ""
            ) {
                isBtnDisable = false;
            } else {
                isBtnDisable = true;
            }
            this.setState({
                isBtnDisable
            });
        }, 100);
    }

    showLayer(layerShow) {
        this.setState(
            {
                layerShow
            },
            () => {
                if (!signInstance) {
                    signInstance = new Signature(this.refs.canvas);
                    signInstance.init();
                }
            }
        );
    }

    getSignatureImg() {
        let img = signInstance.save();
        let isBlank = isCanvasBlank(this.refs.canvas);

        this.setState({
            signatureStr: isBlank ? "" : img
        });
        if(isBlank){
            this.addSinatureErrTip(true);
        }else{
            this.addSinatureErrTip(false);
        }
    }

    clearSignature() {
        signInstance.clear();
    }

    addSinatureErrTip(bool) {
        //
        let container = this.refs.signatureContainer;
        if (bool) {
            container.classList.add("am-input-error");
        } else {
            container.classList.remove("am-input-error");
        }
    }

    render() {
        let {
            rateValue,
            purpostValue,
            interest,
            isBtnDisable,
            borrowerNameErr,
            borrowerIdErr,
            lendersErr,
            borrowerAmountErr,
            borrowerCycleErr,
            borrowDate,
            repaymentDate,
            layerShow,
            signatureStr
        } = this.state;
        let {history} = this.props;
        return (
            <section className="receipt">
                <List>
                    <InputItem
                        type="text"
                        placeholder=""
                        clear={true}
                        maxLength="8"
                        error={borrowerNameErr}
                        onBlur={v => {
                            this.checkAllStatu();
                            this.checkInput("borrowerName", v, "name");
                        }}
                    >
                        
                    </InputItem>
                    <div className="errTip">2-8</div>
                    <InputItem
                        type="text"
                        placeholder=""
                        clear={true}
                        maxLength="18"
                        error={borrowerIdErr}
                        onBlur={v => {
                            this.checkAllStatu();
                            this.checkInput("borrowerId", v, "id");
                        }}
                    >
                        
                    </InputItem>
                    <div className="errTip">18</div>
                    <div
                        className="am-list-item am-input-item am-list-item-middle am-list-item-middle-disable"
                        ref={"signatureContainer"}
                    >
                        <div className="am-list-line">
                            <div className="am-input-label am-input-label-5"></div>
                            <div className="signatureTip" onClick={this.showLayer.bind(this, true)}>
                                {signatureStr == "" ? (
                                    <span></span>
                                ) : (
                                    <img src={signatureStr} />
                                )}
                            </div>
                            <div className="am-input-error-extra" />
                        </div>
                    </div>
                    <div className="errTip"></div>
                </List>
                <List>
                    <InputItem
                        type={"text"}
                        placeholder=""
                        clear={true}
                        maxLength="6"
                        error={lendersErr}
                        onBlur={v => {
                            this.checkInput("lenders", v, "name");
                            this.checkAllStatu();
                        }}
                    >
                        
                    </InputItem>
                    <div className="errTip">2-8</div>
                </List>
                <List>
                    <InputItem
                        type="money"
                        placeholder="0.1~500000"
                        clear
                        maxLength="7"
                        onBlur={v => {
                            this.onBorrowerAmount(v);
                            this.checkInput("borrowerAmount", v, "money");
                            this.checkAllStatu();
                        }}
                        error={borrowerAmountErr}
                    >
                        
                    </InputItem>
                    <div className="errTip">0.1~500000</div>
                    <InputItem
                        type="digital"
                        placeholder="1~356"
                        clear={true}
                        maxLength="3"
                        error={borrowerCycleErr}
                        onBlur={v => {
                            this.onBorrowerCycle(v);
                            this.checkInput("borrowerCycle", v, "cycle");
                            this.checkAllStatu();
                            this.setState({
                                repaymentDate: borrowDate + (v - 1) * 3600 * 24 * 1000
                            });
                        }}
                    >
                        
                    </InputItem>
                    <div className="errTip">1~356</div>
                    <Picker
                        data={RATE}
                        title=""
                        cascade={false}
                        value={[rateValue]}
                        cols={1}
                        onChange={v => {
                            this.onRateChange(v[0]);
                        }}
                        onOk={v => {
                            this.setState({
                                rateValue: v[0]
                            });
                        }}
                    >
                        <List.Item arrow="horizontal"></List.Item>
                    </Picker>
                    <div className="caleRate">{interest}</div>
                    <InputItem
                        type="text"
                        placeholder=""
                        disabled={true}
                        value={dateFormat(borrowDate, "YYYYMMdd")}
                    >
                        
                    </InputItem>
                    <InputItem
                        type="text"
                        placeholder=""
                        disabled={true}
                        value={dateFormat(repaymentDate, "YYYYMMdd")}
                    >
                        
                    </InputItem>
                    <Picker
                        data={PURPOST}
                        title=""
                        cascade={false}
                        cols={1}
                        value={[purpostValue]}
                        onChange={v =>
                            this.setState({
                                purpostValue: v[0]
                            })
                        }
                        onOk={v =>
                            this.setState({
                                purpostValue: v[0]
                            })
                        }
                    >
                        <List.Item arrow="horizontal"></List.Item>
                    </Picker>
                </List>
                <List className="checkbox">
                    <AgreeItem
                        defaultChecked={true}
                        onChange={e => {
                            this.setState({
                                AgreeItemChecked: e.target.checked
                            });
                        }}
                    >
                        &nbsp;
                        <Link to="/serviceAgr"></Link>
                        <Link to="/borrowAgr"></Link>
                    </AgreeItem>
                </List>
                <WingBlank>
                    <WhiteSpace />
                    <Button
                        type={"primary"}
                        onClick={() => {
                            console.log("primary click")
                            history.push("/write");
                        }}
                        disabled={isBtnDisable}
                    >
                        
                    </Button>
                    <WhiteSpace />
                </WingBlank>
                <div className={layerShow ? "layer" : "layer hide"}>
                    <div className="signature">
                        <canvas className="canvas" ref="canvas" />
                        <WingBlank>
                            <WhiteSpace />
                            <Button
                                inline
                                style={{marginRight: "15px"}}
                                onClick={() => {
                                    this.showLayer(false);
                                    this.addSinatureErrTip(true);
                                }}
                            >
                                
                            </Button>
                            <Button
                                type={"warning"}
                                inline
                                style={{marginRight: "15px"}}
                                onClick={this.clearSignature}
                            >
                                
                            </Button>
                            <Button
                                type={"primary"}
                                inline
                                onClick={() => {
                                    this.showLayer(false);
                                    this.getSignatureImg();
                                    this.checkAllStatu();
                                }}
                            >
                                
                            </Button>
                            <WhiteSpace />
                        </WingBlank>
                    </div>
                </div>
            </section>
        );
    }
}



